import React from 'react'
import { Button } from '@mui/material'
import { useNavigate } from 'react-router-dom'

type Props = {
  width?: number
  height?: number
}

const Logo: React.FC<Props> = ({ width = 100, height = 51 }) => {
  const navigate = useNavigate()

  return (
    <Button variant="text" onClick={() => navigate('/')}>
      <svg id="logo" width={width} height={height} viewBox="0 0 100 51" fill="none" xmlns="http://www.w3.org/2000/svg"
        xmlnsXlink="http://www.w3.org/1999/xlink">
        <rect width="100" height="51" fill="url(#pattern0)"/>
        <defs>
          <pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1">
            <use xlinkHref="#image0_506_7196" transform="matrix(0.00172117 0 0 0.00337484 0 -0.00116432)"/>
          </pattern>
          <image id="image0_506_7196" width="581" height="297"
            xlinkHref="data:image/png;base64,"/>
        </defs>
      </svg>
    </Button>
  )
}

export default Logo
